import * as echarts from 'echarts';
import start from './start.js'
import line from './line.js'
import areaStackGradient from './areaStackGradient.js'
import lineFail from './lineFail.js'

export default{
	start,
	line,
	lineFail,
	areaStackGradient,
	setChart(id,option){
		let myChart = echarts.init(document.getElementById(id));
		myChart.setOption(option);
		setTimeout(function (){
			    window.onresize = function () {
			    	myChart.resize();
			    }
			},200)
		
	}
}