import config from './config.js';
import { gql,request  } from 'graphql-request'
let {req,reqVar}  = config;
export default{
	getTokenLists(){
		const DATA = gql`
		  {
		    tokens(orderBy: volumeUSD, orderDirection:desc, first:50){
		                    id,
		                    symbol,
		                    name
		     }
		  }
		`;
		return req(DATA);
	},
	searchCoin(addr){
		
			const DATA = gql`
			  query($addr:String!){
			    token(id: $addr){
			          id,
			          symbol,
			          name
			     }
			  }
			`
			const variables = {
			  addr:addr
			}
			return reqVar(DATA,variables);
		
	}
		
	
}