import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
const routes = [

  {
    path: '/',
    component: () => import('@/views/menu/menu'),
    children: [
      {
        path: '/', // 默认子路由
        component: () => import('@/views/home/home')
      },
      {
        path: '/home',
        component: () => import('@/views/home/home')
      },
      {
        path: '/liquidity', 
        component: () => import('@/views/liquidity/liquidity')
      },
      {
        path: '/crowdpooling', 
        component: () => import('@/views/crowdpooling/crowdpooling')
      },
      {
        path: '/ming', 
        component: () => import('@/views/ming/ming')
      },
      {
        path: '/dashboard', 
        component: () => import('@/views/developer/dashboard')
      },
      {
        path: '/crowd', 
        component: () => import('@/views/developer/crowd')
      },
      {
        path: '/pool', 
        component: () => import('@/views/developer/pool')
      },
      {
        path: '/token', 
        component: () => import('@/views/developer/token')
      },
      {
        path: '/create', 
        component: () => import('@/views/developer/create')
      },
      {
        path: '/pond', 
        component: () => import('@/views/developer/pond')
      },
      {
        path: '/launch', 
        component: () => import('@/views/developer/launch')
      },
    ]
  }
]
const router = new VueRouter({
  routes
})
export default router
