import * as echarts from 'echarts';
export default{

	    color: ['#80FFA5', '#00DDFF', '#37A2FF', '#FF0087', '#FFBF00'],
	    title: {
	        text: '渐变堆叠面积图',
			show:false
	    },
	    tooltip: {
	        trigger: 'axis',
	        axisPointer: {
	            type: 'cross',
	            label: {
	                backgroundColor: '#6a7985'
	            }
	        },
			show:false
	    },
	    legend: {
	        data: ['Line 1', 'Line 2', 'Line 3', 'Line 4', 'Line 5'],
			show:false
	    },
	    toolbox: {
	        feature: {
	            saveAsImage: {}
	        },
			show:false
	    },
	    grid: {
	        left: '-5%',
	        right: '0%',
	        bottom: '0%',
	        containLabel: true,
	    },
	    xAxis: [
	        {
	            type: 'category',
	            boundaryGap: false,
	            data: ['周一', '周二', '周三', '周四'],
				show:false
	        }
	    ],
	    yAxis: [
	        {
	            type: 'value',
				show:false
	        }
	    ],
	    series: [
	        {
	            name: 'Line 1',
	            type: 'line',
	            stack: '总量',
	            smooth: true,
	            lineStyle: {
	                width: 0
	            },
	            showSymbol: false,
	            areaStyle: {
	                opacity: 0.5,
	                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
	                    offset: 0,
	                    color: 'rgb(55, 55, 57)'
	                }, {
	                    offset: 1,
	                    color: '#1a1a1b'
	                }])
	            },
	            emphasis: {
	                focus: 'series'
	            },
	            data: [50, 60, 40, 50]
	        },
	        
	    ]
	
}